body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  padding: 0;
  border: 0;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
  color: #c0a26d;
}

.title-header {
  padding: 15px 15px 9px 15px;
  font-size: 14px;
  color: rgb(136, 136, 136);
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.trapezoid {
  margin-left: auto;
  margin-right: auto;
  border-radius: 30px;
  background-color: #dbbf7a;
  text-align: center;
  width: 170px;
  font-weight: bold;
  line-height: 35px;
  color: #f94747;
}

.am-card {
  background-color: rgb(162, 221, 235) !important;
}

.am-wingblank {
  background-color: rgb(162, 221, 235);
}

.green-back {
  background-color: rgb(162, 221, 235);
}

.medium-font {
  font-size: 1rem;
  font-weight: bold;
}

.big-font {
  font-size: 1.5rem;
  font-weight: bold;
}

.am-list-item {
  position: relative;
  display: flex;
  padding-left: 15px;
  min-height: 44px;
  background-color: rgb(162, 221, 235) !important;
  vertical-align: middle;
  overflow: hidden;
  transition: background-color 200ms;
  align-items: center;
}

:root:root {
  --adm-button-border-radius: 14px;
  /* --adm-color-background: black; */
}

.am-button {
  padding: 0px 5px !important;
  font-size: 14px !important;
  height: 30px !important;
  line-height: 30px !important;
  /* color: #d81f07 !important; */
  background-color: rgb(240, 153, 73) !important;
  border: 1PX solid rgb(97, 97, 97) !important;
  border-radius: 5px !important;
}

